@tailwind base;
@tailwind components;
@tailwind utilities;

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #F0DB7B;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #080E3B;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  background: rgb(255,234,65);
  background: rgb(240,189,18);
  background: -moz-linear-gradient(90deg, rgba(240,189,18,1) 0%, rgba(240,219,123,1) 50%, rgba(240,189,18,1) 100%);
  background: -webkit-linear-gradient(90deg, rgba(240,189,18,1) 0%, rgba(240,219,123,1) 50%, rgba(240,189,18,1) 100%);
  background: linear-gradient(90deg, rgba(240,189,18,1) 0%, rgba(240,219,123,1) 50%, rgba(240,189,18,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f0bd12",endColorstr="#f0bd12",GradientType=1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
